        p Los indicadores y la aplicabilidad de la analtica de datos en las organizaciones deben manejarse bajo necesidades reales, preguntas fundamentales y la identificacin de qu respuestas requiere el negocio para optimizar los tiempos y asertividad de la toma de decisiones.
      
