    #t_3_4.titulo-segundo.color-acento-contenido(data-aos="zoom-in-left")
      h2 3.4 Tcnicas de solucin de problemas (modelacin de datos)

    p.mb-3(data-aos="zoom-in-left") Si bien existen varias metodologas, todas coinciden en una manera genrica para la solucin de un proyecto analtico propuesto por  Davenport, con algunas variables.
    p.mb-3(data-aos="zoom-in-left") #[b Esta metodologa tiene tres grandes etapas:]

    .row.align-items-center.justify-content-start(data-aos="zoom-in-left")
      .col-sm-12.col-lg-11
        .tarjeta-avatar-b.mb-5
