          .tarjeta.bg26
            .p-4
              h4.mb-4 Definicin del problema
              p Para reconocer el problema, se debe partir de las necesidades surgidas a partir de la experiencia para tomar una decisin o accin; es decir, qu necesidades o inconvenientes se han tenido cuando la informacin, reportes o conocimiento de algn dato importante no est disponible en el momento y exactitud para tomar decisiones o ejecutar acciones. Es importante gestionar las expectativas y documentar las necesidades o problemas puntuales.
              p #[b En esta fase, una clave es identificar objetivos que no se cumplen por la falta de un proyecto analtico.]

    .row.align-items-center.justify-content-start(data-aos="zoom-in-left")
