               ul.lista-ul--color.mx-2.mt-4
                li.mb-0.pb-0 
                  i.fa.fa-check
                  p  Modelar y seleccionar variables que representan el problema y determinar las variables que forman parte de l.
                li.mb-0.pb-0 
                  i.fa.fa-check
                  p  Recopilar datos para identificar dnde se consiguen datos para las variables del modelo.
                li.mb-0.pb-0 
                  i.fa.fa-check
                  p  Anlisis de datos. Determinar naturaleza, fuentes y gestin para el acceso tcnico de la informacin.
                li.mb-0.pb-0 
                  i.fa.fa-check
                  p  Desarrollar componentes tcnicos y generacin de reportes con informacin de valor y conocimiento del negocio.

        
    .row.align-items-center.justify-content-start(data-aos="zoom-in-left")
      .col-sm-12.col-lg-11
        .tarjeta-avatar-b.mb-5
