    p.mb-0(data-aos="zoom-in-left") La modelacin de las soluciones #[i BI] depende de las reglas de negocio y los requisitos o problemas a resolver. En los apartados siguientes se detallan las arquitecturas disponibles y el proceso del tratamiento de datos a partir de una mirada de anlisis e inteligencia de negocio.                
        
    Separador

    #t_3_5.titulo-segundo.color-acento-contenido(data-aos="zoom-in-left")
      h2 3.5 Metodologas de anlisis (Kimball, Inmon)

    p(data-aos="zoom-in-left") En lo relacionado a la planeacin y diseo de las bodegas de datos (#[i DWH]), se plantean dos arquitecturas:

    .mb-4.d-flex.bg-img-title(data-aos="zoom-in-left")
      .d-flex.flex-wrap.align-items-center.pe-4
        h3.col.px-4.mb-0.text-white.aos-init.aos-animate(style='width: 300px;').bg-img-blue Modelo  Kimball
        img.col-lg.d-flex.aos-init.aos-animate(src='@/assets/curso/temas/103.svg').icon-blue


    .row.mb-5(data-aos="zoom-in-left")
      .col-md-12.bloque-2
        .bloque-texto-b.bloque-texto-b-izquierda.p-4.mb-4.color-der
          .bloque-texto-b__texto
            p.mb-3 La metodologa para el diseo de #[i DWH] propuesta por Ralph Kimball se centra en copiar las bases de datos transaccionales en un modelo optimizado para consultas de analtica. Para integrar varias reas o disciplinas del negocio se emplean #[i data marts], que son bases de datos que surgen a partir del proceso de transformacin del #[i ETL] por cada rea o divisin del negocio.#[br]#[br]
            p #[b Kimball propone que estos  #[i Data Mart]  deben crearse primero para proporcionar capacidades analticas;] en pasos siguientes del flujo de datos se integran en una bodega de datos empresarial de manera integral. Una desventaja de este modelo es que al cambiar los #[i Data Marts]  se podran perder dimensiones en los reportes diseados. Adicionalmente, se podran presentar datos redundantes en varios #[i Data Mart]. No obstante, es el modelo ms usado en las soluciones #[i BI], no indicando esto que sea mejor (consultar figura).
            
    .mx-auto.mb-5
      .titulo-sexto.color-acento-contenido
        h5 Figura 1.
        span
          | Modelo Kimball
    .fullview.mb-4
      figure.p-5
        img(src='@/assets/curso/temas/Tema3/19.svg', alt='La ilustracin presenta el modelo Kimball, que es un enfoque de diseo para almacenes de datos. El proceso comienza con la recopilacin de datos de diferentes fuentes de datos mediante #[i OLTP] (procesamiento de transacciones en lnea). Estos datos se transforman y cargan en datamarts a travs del proceso ETL (extraccin, transformacin y carga). Los datamarts se consolidan en un almacn de datos (data warehouse), que luego se organiza en un cubo de datos #[i OLAP] (procesamiento analtico en lnea). Finalmente, los datos estructurados en el cubo de datos se utilizan para generar informes en la capa de reportes. ')

    .mb-4.d-flex.bg-img-title(data-aos="zoom-in-left")
      .d-flex.flex-wrap.align-items-center.pe-4
        h3.col.px-4.mb-0.text-white.aos-init.aos-animate(style='width: 300px;').bg-img-blue Modelo Inmon
        img.col-lg.d-flex.aos-init.aos-animate(src='@/assets/curso/temas/103.svg').icon-blue


    .row.mb-5(data-aos="zoom-in-left")
      .col-md-12.bloque-2
        .bloque-texto-b.bloque-texto-b-izquierda.p-4.mb-4.color-der
          .bloque-texto-b__texto
            p.mb-5 No es muy diferente a Kimball, los principios de transformacin y carga a los #[i DWH] contienen los mismos elementos en su arquitectura, sin embargo, la metodologa de   Bill Inmon  plantea que los  #[i data marts] deben ir despus del almacenamiento de #[i DWH].#[br]#[br]
            p #[b La metodologa  Inmon comienza por generar un arreglo de todos los datos corporativos en el #[i DWH]], para luego identificar y dividir reas para la generacin de los #[i Data Mart]. En este modelo se normalizan ms los datos, conteniendo menos redundancia; es un poco ms complejo de usar a nivel comercial o abierto, sin embargo, podra ser ms administrado en el sentido que se crean los #[i data marts] y cada rea del negocio podra usarlos creando sus propios cubos de datos o reportes (consultar figura).
    .row(data-aos="zoom-in-left")
      .col-md-12.mx-auto.mb-0
        .titulo-sexto.color-acento-contenido
          h5 Figura 2.
          span
            | Modelo Inmon
    .fullview.mb-4
      figure.p-5
        img(src='@/assets/curso/temas/Tema3/20.svg', alt='La ilustracin presenta el modelo Inmon, otro enfoque de diseo para almacenes de datos. En este modelo, los datos se recopilan de diferentes fuentes de datos mediante #[i OLTP] (procesamiento de transacciones en lnea) y se transforman y cargan en un almacn de datos (#[i data warehouse]) a travs del proceso ETL (extraccin, transformacin y carga). A partir del almacn de datos, se crean datamarts especficos para diferentes reas de anlisis. Estos datamarts se estructuran en cubos de datos #[i OLAP] (procesamiento analtico en lnea), que se utilizan para generar informes en la capa de reportes. ')

