    #t_3_6.titulo-segundo.color-acento-contenido(data-aos="zoom-in-left")
      h2 3.6 Verificacin de valores y escalas

    p.mb-4(data-aos="zoom-in-left") Los cuadros de mando o #[i Dashboard], permiten hacer monitoreo de los procesos mostrando informacin importante a travs de elementos grficos de fcil entendimiento y con actualizaciones peridicas.
    p.mb-4(data-aos="zoom-in-left") Los valores que presentan deben ser indicadores de procesos, tareas o situaciones importantes para el negocio, su escalabilidad y visualizacin deben aparecer claramente. Para algunos valores podran presentarse escalas (ejemplo, valores de ventas), para otros no (ejemplo, comparaciones porcentuales).
    p.mb-4(data-aos="zoom-in-left") #[b Entre los elementos ms importantes de los cuadros de mando, su tipo de valor y escalas se pueden mencionar:]

    .row.mb-0(data-aos="zoom-in-left")
      .col-sm-12.col-md-12.col-lg-8.mb-5.mb-md-0
        LineaTiempoD.color-secundario
          p.text-content-cus(numero="1" titulo="Tablas") se conforma de matriz, presenta mltiples datos, puede ser esttica o dinmica segn las reglas del negocio y caractersticas de la informacin que representa. Presenta datos estructurados por dimensiones (tipo #[i OLAP]) en algunos casos, y su escalabilidad se limita al redondeo de cifras configurando la cantidad de decimales de los datos numricos flotantes.
          
          p.text-content-cus(numero="2" titulo="Mtricas") los valores surgen como resultado de una actividad especfica y las medidas son el resultado de estas actividades en su conjunto o segmentacin, siguiendo o no una serie de condiciones y operaciones. Por lo general las mtricas se denominan KPI.
          
          p.text-content-cus(numero="3" titulo="Listas") comnmente formadas por KPI. En caso de que el cuadro de mando solo est formado por este tipo de elemento, se denomina #[i Scorecard].
          
          p.text-content-cus(numero="4" titulo="Grficos") el fin es mostrar datos con alto impacto visual, que sirva para obtener informacin acumulativa o calculada. Al igual que las tablas pueden tener mltiples dimensiones, pero se vern con mejor presentacin y dimensin los datos. Se debe poner especial atencin en el aspecto de la escalabilidad, pues hay datos que por su tamao deben ser escalados para que se compare y se note las diferencias; sin embargo, ocasionalmente no es siempre recomendable, por ejemplo: un grfico de barras si presenta la cantidad de dinero vendido, es factible escalar, para que el valor no empiece en 0 (cero), sino por ejemplo en 10 millones (segn el tamao de las cifras), pero si estas barras presentan porcentajes, es necesario que la grfica siempre empiece desde el 0%, para que se note bien la dimensionalidad de los datos. #[br] Este es el elemento ms comn y de mayor variabilidad, existen mltiples opciones de visualizadores grficos en diferentes segmentos (barras, circulares, mayas, etc.).

          p.text-content-cus(numero="5" titulo="Mapas") este elemento permite mostrar informacin geolocalizada. Aplica a los datos de ubicacin, de esta manera se dimensiona la ubicacin de las cifras que se estn representando.

          p.text-content-cus(numero="6" titulo="Alertas visuales") por lo general, al momento de desarrollar los cuadros de mando se pueden incluir a la programacin de alertas automticas con el fin de informar un acontecimiento crtico, ya sea por fechas u otro evento que suceda.

          p.text-content-cus(numero="7" titulo="Mens de navegacin") estos elementos visuales, ya sean en texto o botones, facilitan al usuario navegar y realizar operaciones interactivas en los elementos del cuadro de mando.

      .col-sm-12.col-md-12.col-lg-4
        
        .tarjeta.bg35.p-4.mt-5      
          p.mb-0.py-3.px-2 Ejemplo de componentes de un cuadro de mando o #[i Dashboard]:
        img(src='@/assets/curso/temas/Tema3/17.png')



    Separador

